import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons

// core components
import styles from "./jss/customHeaderStyle";

const useStyles = makeStyles(styles);

export default function CustomHeader(props) {
  const classes = useStyles();
  const { className, children, icon, color, ...rest } = props;
  const customHeaderClasses = classNames({
    [classes.customHeader]: true,
    [classes.customHeaderIcon]: icon,
    [className]: className !== undefined
  });
  return (
    <div
      className={customHeaderClasses}
      style={{ background: color }}
      {...rest}
    >
      <div {...rest}>{children}</div>
    </div>
  );
}

CustomHeader.propTypes = {
  className: PropTypes.string,
  icon: PropTypes.bool,
  color: PropTypes.string,
  children: PropTypes.node
};
